
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.content{
  height: 120vh;
  background-image: url("./images/pathway-middle-green-leafed-trees-with-sun-shining-through-branches.jpg");
  background-size: cover;
  background-position: left;
  background-repeat: no-repeat;
}



.gameboard .space{
  display: grid;
  justify-content: center;
  align-content: center;
  border: 2px solid black;
  width: 2rem;
  height: 2rem;
  background-color: rgba(195, 195, 174, .6);
  color: #052622;
}

.gameboard{
  width: 16rem;
  height: 16rem;
  display: grid;
  grid-template-columns: repeat(8,1fr);
  grid-template-rows: repeat(8,1fr);
  justify-self: end;
  margin-right: 50px;
}
.gameboard2{
  width: 18rem;
  height: 18rem;
  display: grid;
  grid-template-columns: repeat(12,1fr);
  grid-template-rows: repeat(12,1fr);
  justify-self: center;
  /* margin-right: 50px; */
  /* align-items: center; */
}

.gameboard2 .space{
  display: grid;
  justify-content: center;
  align-content: center;
  border: 1px solid black;
  /* width: 1rem;
  height: 1rem; */
  background-color: rgba(195, 195, 174, .6);
  color: #052622;
}
.gameboard3{
  width: 18rem;
  height: 18rem;
  display: grid;
  grid-template-columns: repeat(16,1fr);
  grid-template-rows: repeat(16,1fr);
  justify-self: center;
  /* margin-right: 50px; */
  /* align-items: center; */
  background-image: url("./images/IMG_4723.JPG");
  background-position: 50%;
  background-size: contain;
  background-repeat: no-repeat;
}

.gameboard3 .space{
  display: grid;
  justify-content: center;
  align-content: center;
  border: 1px solid black;
  /* width: 1rem;
  height: 1rem; */
  /* background-color: rgba(195, 195, 174, .6); */
  /* color: #052622; */
  color: white;
  background-color: rgb(0, 0, 0,.3);
  
}

.gameboard3 .hidden {
  background-color: rgba(160, 181, 138, 1);

}

.sprite,
.crying,
.sweating,
.winning{
  height: 300px;
  width: 300px;
  background: cover;
  background-repeat: no-repeat;
  background-size: cover;
  margin-top: 2.5rem;
}
.sprite{
  background-image: url("../src/images/sprite2.gif");
}
.crying{
  background-image: url("../src/images/crying.gif");
  background: cover;
}
.sweating{
  background-image: url("../src/images/sweating.gif");
}
.winning{
  background-image: url("../src/images/winning.gif");
}

.board{
  display:grid;
  /* grid-template-columns: 1fr 5fr; */
 
}

/* footer{
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
} */

.start-message{
  display: grid;
  justify-content: center;
  align-items: center;
  margin: 5vw;
  margin-right: 5vw;
  width: 75vw;
  height: auto;
  padding:5vw;
 
  

}
.start-message>div,.start-message>h2{
  background-color:rgba(255, 255, 255,.7);
  padding: 5vw;
 
}

.App-header{
  background-color: #d4bf88;
  background-image: url("./images/wood-texture-close.jpg");
  background-size: cover;
  height: 2.5rem;
margin-top: -1.2rem;
  padding-top: .5rem;
  font-size: 1.5rem;
  font-family: 'Creepster', cursive;
  /* font-size: 80px; */
  color: white;
  font-weight: bold;
  /* margin-left: -1rem; */
  /* padding: 10px; */
  mix-blend-mode: multiply;
  text-shadow: 5px 4px 11px rgb(0,0,0), 0 2px 5px rgb(0,0,0);
  /* text-align: center; */

}
header>img{
  margin-right: 5px;
}
.display{
  display: flex;
}

.counter,.message{
  background-color: rgba(255, 255, 255, .8);
  width: 4.5rem;
  height: 2.6rem;
  padding-top: 4px;
  margin-left: 5px;
  border-radius: 3px;
  border: 1px solid black;
  margin-bottom: 3px;


  text-align: center;
  /* margin-top: -1.9rem; */
  
}

.message{
  width: 6rem;
}

button{
  margin-left: 70vw;
  width: 6.5rem;
  height: 2rem;
  border-radius: 5px;
}

.main{
  display: grid;
  grid-template-columns: 1fr 1fr
 
}

footer{
  background-color: #d4bf88;
  display: flex;
  justify-content: space-around;
 
  align-items: center;
  font-size: clamp(1rem, -0.875rem + 8.333vw, 3.5rem);
  font-family: 'Creepster', cursive;
  color: rgb(61, 61, 61);
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 100px;
  z-index: 1;
  border-top: 2px solid #052622;
  box-shadow: inset 0 0 10px #242424;
}

footer:first-child{
 
}

.gameboard .hidden,
.gameboard2 .hidden{
  background-color: rgba(160, 181, 138, .9);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media only screen and (max-width: 600px) {
  html, body {width: auto!important; overflow-x: hidden!important} 

  .main{
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr;
  }
  .counter,.message{ 
    /* margin-top: -1.9rem; */
  }
  .gameboard{
    justify-self: center;
    width: 80vw;
    height: 80vw;
  }
  
  .gameboard .space{
    width: 10vw;
    height: 10vw;
  }

  .gameboard2,.gameboard3{
    /* justify-self: start; */
    width: 96vw;
    height: 96vw;
  }
  /* .gameboard2 .space{
    width:6vw;
    height: 6vw;
  } */
  .board{
    margin-left: 2vw;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 10fr;
    height: 96vw;
  }
  .content{
    background-position: 50% 20%;
    height: 200vw;
    /* background-size: 300vh; */
 
  }
  .display{
    margin-top: -2rem;
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
    pointer-events: none;
  }

  .go-back{
    margin-left: 50vw;
  }

  .crying,
  .sprite,
  .sweating,
  .winning{
    margin: -1vw;
    pointer-events: none;
    width: 80vw;
    height: 80vw;
    z-index: 1;
  }

  .nav-button{
    /* display: relative; */
    margin-top: 5px;
  }

  Footer{
   position: relative;
   height: 20vw;
   margin-top: -18vw;
  }

}

